import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
// import BtnEdit from 'components/common/button/BtnEdit';
// import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Col, Input, Button } from 'antd';
import { insertScoreBag } from 'dump/data';
import CreditClass from './CreditClass';
// import ExamRoom from './ExamRoom';
// import YearlyClass from './YearlyClass';

// const { TabPane } = Tabs;

const columns = [
  {
    title: 'Số phách',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Thi (60)',
    dataIndex: 'thi_60',
    render: () =>
      insertScoreBag.length >= 1 ? (
        <Fragment>
          <Input />
        </Fragment>
      ) : null,
  },
  {
    title: 'TBCHP',
    dataIndex: 'tbchp',
    key: 'tbchp',
  },
  {
    title: 'Ghi chú',
    dataIndex: 'note',
    render: () =>
      insertScoreBag.length >= 1 ? (
        <Fragment>
          <Input />
        </Fragment>
      ) : null,
  },
];

class InsertScoreBagPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Nhập điểm theo túi thi">
        <Row>
          <Col span={24}>
            <CreditClass />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={insertScoreBag} columns={columns} />
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'center' }}>
            <Button type="primary">LƯU</Button>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <InsertScoreBagPage />
  </Page>
);



