import React, { Component } from 'react';
import { Col, Form, Select } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';

const { Option } = Select;

@Form.create({ name: 'formTeacher' })
class CreditClass extends Component {
  state = {};
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="inline" onSubmit={this.handleSearch} className="login-form">
        <Col span={8}>
          <Form.Item label="Học kỳ">
            {getFieldDecorator('hk', { initialValue: '' })(
              <Select defaultValue="lucy" style={{ width: 200 }}>
                <Option value="lucy">01</Option>
              </Select>
            )}
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item label="Năm học">
            {getFieldDecorator('khoa', { initialValue: '' })(
              <Select defaultValue="lucy" style={{ width: 200 }}>
                <Option value="lucy">2013-2014</Option>
              </Select>
            )}
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item label="Hệ">
            {getFieldDecorator('dot', { initialValue: '' })(
              <Select defaultValue="lucy" style={{ width: 200 }}>
                <Option value="lucy">Cao đẳng chính quy</Option>
              </Select>
            )}
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item label="Khoa">
            {getFieldDecorator('khoa', { initialValue: '' })(
              <Select defaultValue="lucy" style={{ width: 200 }}>
                <Option value="lucy">Khoa chung</Option>
              </Select>
            )}
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item label="Môn học">
            {getFieldDecorator('subject', { initialValue: '' })(
              <Select defaultValue="lucy" style={{ width: 200 }}>
                <Option value="lucy">Anh Văn 1 (AV1)</Option>
              </Select>
            )}
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item label="Lần thi">
            {getFieldDecorator('class', { initialValue: '' })(
              <Select defaultValue="lucy" style={{ width: 200 }}>
                <Option value="lucy">01</Option>
              </Select>
            )}
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item label="Đợt thi">
            {getFieldDecorator('class', { initialValue: '' })(
              <Select defaultValue="lucy" style={{ width: 200 }}>
                <Option value="lucy">01</Option>
              </Select>
            )}
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item label="Túi thi">
            {getFieldDecorator('class', { initialValue: '' })(
              <Select defaultValue="lucy" style={{ width: 200 }}>
                <Option value="lucy">01</Option>
              </Select>
            )}
          </Form.Item>
        </Col>
        <Col span={24} style={{ textAlign: 'right' }}>
          <div className="btn-group">
            <BtnSearch />
            <BtnClear />
          </div>
        </Col>
      </Form>
    );
  }
}

export default CreditClass;



